.container-home {
  background: url("../../assets/home-bg4.jpg") no-repeat center center/cover;
  height: fit-content;

  .content-home {
    @extend %flexCenter;
    flex-direction: column;
    min-height: 100%;

    background-color: rgba($black, 0.7);
    padding: 50px 0;

    .name,
    .skills,
    .secondary-skills {
      width: 35%;
      text-align: center;
      white-space: nowrap;
    }

    .name {
      font-size: clamp(0.8em, 2vw, 1.5em);
      color: $clear-grey;
    }

    .skills {
      color: white;

      ul li {
        display: flex;
        align-items: center;
        h3 {
          min-width: 70px;
          font-size: clamp(10px, 1.5vw, 16px);
        }

        .level {
          position: relative;
          min-width: 75px;
          width: 100%;
          height: 5px;
          margin-right: 5px;
          border-radius: 5px;
          background-color: rgb(235, 235, 235);
          overflow: hidden;
          box-shadow: 0 0 2px 0px white;

          &::after {
            content: "";
            position: absolute;
            top: 0;
            display: block;
            height: 100%;
            box-shadow: 0px 0px 10px 2px black;
          }
          &.html::after {
            width: 95%;
            background-color: #dd4d25;
          }
          &.css::after {
            width: 90%;
            background-color: #146eb0;
          }
          &.sass::after {
            width: 75%;
            background-color: #c76395;
          }
          &.js::after {
            width: 75%;
            background-color: #efd81d;
          }
          &.react::after {
            width: 70%;
            background-color: #00d5f7;
          }
          &.next::after {
            width: 55%;
            background-color: #000;
          }
        }
      }
    }

    ul.secondary-skills {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      transition-delay: all 1s;

      li.card-skill {
        font-size: clamp(8px, 1.5vw, 12px);
        border-radius: 5px;
        padding: 8px;
        margin: 5px 5px 0 0;
        background-color: $secondary;
        color: $black;
        font-weight: 600;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .container-home .content-home {
    .name,
    .skills,
    .secondary-skills {
      width: 45%;
    }
  }
}
@media screen and (max-width: 950px) {
  .container-home .content-home {
    .name,
    .skills,
    .secondary-skills {
      width: 60%;
    }
  }
}
@media screen and (max-width: 600px) {
  .container-home .content-home {
    .name,
    .skills,
    .secondary-skills {
      width: 80%;
    }
  }
}
@media screen and (max-width: 350px) {
  .container-home .content-home {
    .name h1 {
      white-space: normal;
    }
    .name,
    .skills,
    .secondary-skills {
      width: 100%;
    }
  }
}
