.container-presentation {
  @extend %flexCenter;
  padding: 10vw 0;

  .txt {
    font-size: clamp(12px, 1.5vw, 22px);
    font-weight: 700;
    width: 60%;

    p {
      margin: 20px 0;
      color: $black;
    }
  }
  .link {
    color: $secondary;
    text-decoration: underline;
  }
}

@media screen and (max-width: 600px) {
  .container-projects .project {
    .container-presentation .txt {
      width: 80%;
    }
  }
}
