.container-form {
  padding: 5vw 0;

  form {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: auto;
    padding-bottom: 25px;
    font-weight: 600;
    color: $primary;
    text-align: center;

    #mail,
    #message,
    #name {
      margin-bottom: 10px;
      color: $dark-grey;
      font-size: 17px;
      border: none;
      outline: none;
      padding: 5px;
      background: #f1f1f1;
      background: linear-gradient(230deg, #f9f9f9, transparent);
      border-bottom: 3px solid $secondary;
      resize: none;

      &::placeholder {
        color: hsl(210, 14%, 85%);
      }

      &:focus {
        background: linear-gradient(230deg, #f3f3f3, transparent);
      }
    }

    label {
      margin-bottom: 10px;
      color: $black;
    }

    button {
      cursor: pointer;
      border: none;
      padding: 10px;

      border-bottom: 3px solid $secondary;
      font-size: 16px;
      font-weight: 600;
      font-family: "Roboto", sans-serif;
      background: linear-gradient(230deg, #f9f9f9, transparent);
      transition: all 0.1s;
      color: $black;

      &:hover {
        background: linear-gradient(130deg, #f0f0f0, transparent);
      }

      &:active {
        box-shadow: inset 0 2px 4px black;
      }
    }

    .form-message {
      display: block;
      font-weight: initial;
      position: absolute;
      bottom: 0px;
      animation: wiggle 1s;
    }
  }
}

@keyframes wiggle {
  0% {
    transform: translate(0px, 0px);
  }

  25% {
    transform: translate(-10px, 0px);
  }
  50% {
    transform: translate(10px, 0px);
  }
  75% {
    transform: translate(-10px, 0px);
  }

  100% {
    transform: translate(0px, 0px);
  }
}

// responsive

@media screen and (max-width: 1000px) {
  .container-form form {
    width: 60%;
  }
}

@media screen and (max-width: 400px) {
  .container-form form {
    width: 80%;
  }
}
